.topbar
{
    &.title
    {
        text-align: center;
        color: white;
        font-size: xx-large;
    }
    &.description
    {
        text-align: left;
        color: white;
        font-size: 1.25rem;
        font-weight: lighter;
        width: 100%;
        margin: 0 auto;
        max-width: 1400px;
        display: flex;
        justify-content: flex-start;

    }

    &-stick-top
    {
        position: sticky;
        top: 50px;
        height: 55px;
        width: 100%;
        border-bottom: 1px solid $bg-lightest;
        background: rgba(14,19,60,1);
        z-index: 10;
    }
}

.topbar-wrapper
{
    display: flex;
    width: 100%;
    align-items: center;
    background-color: transparent;
    flex-direction: column;
    padding: 1rem 1rem 0;
    transform: scale(1);
}
