.auth 
{
    &-simple
    {
        display: flex;
        flex-direction: row;
        align-items: center;    
        justify-content: space-between;
        .schemes-title 
        {
            display:none;
        }
        label {
            margin-right: .5rem;
            margin-top: .25rem;
        }
        .info
        { 
            &-wrapper
            {
                display: flex;
                align-items: center;
                z-index: 2;
                padding-left: 10px;
                position: relative;
            }
            
            &-inner
            {
                position: absolute;
                background-color: $white !important;
                padding: 10px;
                right: 1.75rem;
                top: -50%;
                width: 18vw;
                max-width: 20rem;
                border-radius: 4px;
                box-shadow: 0 0 6px 1px rgba(0,0,0,.75);

                .header
                {
                    margin: 0;
                    h1
                    {
                        @include text_headline();
                        text-transform: none;
                        font-size: large; 
                        color: $info-header-color;
                        margin: 0 0 5px 0;
                    }
                }
                .body
                {
                    margin: 0 0 0 20px;
                    
                    &-inner
                    {
                        margin: 10px 0;
                        h1
                        {
                            @include text_body();
                            margin: 0;
                            font-size: medium; 
                            color: $info-body-color;
                            font-weight: 50;
                        }
                    }
                }
            }
        }

        input, label
        {
            width: 100%;
            background-color: $bg-light-blue;
            border: thin solid $auth-input-border;
            color: $auth-input-text;

            &:focus
            {
                outline: none;
                border: thin solid $form-focus-border;
            }

            &::placeholder 
            {
                color: $auth-input-placeholder;
            }
        }

        .basic
        {
            width: 100%;
        }
    
    }
}

.auth-input
{
    &.error
    {
        animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
        
        &::placeholder
        {
            color: $auth-input-placeholder-error !important;
        }
    }
    &.encode
    {
        &:not(:focus)
        {
            -webkit-text-security: disc;
        }
    }
}

.auth-btn-wrapper
{
    padding: 20px 0;

    .btn-done {
      margin-right: 1em;
    }
}

.auth-wrapper
{
    display: flex;
    flex-direction: column;

    flex: 1;
    justify-content: flex-end;

    .authorize
    {
        margin-right: 10px;
    }
}

.auth-container
{
    margin: 0 0 10px 0;

    &:last-of-type
    {
        margin: 0;
        padding: 10px 20px;

        border: 0;
    }

    h4
    {
        margin: 5px 0 15px 0 !important;
    }

    .wrapper
    {
        margin: 0;
        padding: 0;
    }

    .errors
    {
        font-size: 12px;

        padding: 10px;

        border-radius: 4px;

        @include text_code();
    }
}

.scopes
{
    h2
    {
        font-size: 14px;

        @include text_headline();
    }
}

.scope-def
{
    padding: 0 0 20px 0;
    font-size: smaller;
}

.no-auth
{
    h1
    {
        font-size: larger;
        font-weight: 550;
    }
}